
.box {
  max-width: 1170px;
  width: 100%;
  margin: 0 auto;
  position: relative;
  padding: 0 14px;
  box-sizing: border-box;
}
.box__fluid {
  max-width: 100%;
}
.box:after,
.box:before,
.box__row:before,
.box__row:after {
  content: '';
  width: 100%;
  display: table;
  clear: both;
}
.box__row {
  margin-left: -7px;
  margin-right: -7px;
}
.col__md_12,
.col__md_11,
.col__md_10,
.col__md_9,
.col__md_8,
.col__md_7,
.col__md_6,
.col__md_5,
.col__md_4,
.col__md_3,
.col__md_2,
.col__md_1,
.col__sm_12,
.col__sm_11,
.col__sm_10,
.col__sm_9,
.col__sm_8,
.col__sm_7,
.col__sm_6,
.col__sm_5,
.col__sm_4,
.col__sm_3,
.col__sm_2,
.col__sm_1,
.col__xs_12,
.col__xs_11,
.col__xs_10,
.col__xs_9,
.col__xs_8,
.col__xs_7,
.col__xs_6,
.col__xs_5,
.col__xs_4,
.col__xs_3,
.col__xs_2,
.col__xs_1 {
  float: left;
  padding: 0 7px;
  position: relative;
}
.col__md_12 {
  width: 100%;
}
.col__md_4 {
  width: 33.3%;
}
.col__md_6 {
  width: 50%;
}
.col__empty {
  padding: 0 7px;
  position: relative;
  float: left;
}
@media screen and (max-width: 768px) {
.col__xs_6 {
    width: 50%;
}
}
html {
  font-size: 17px;
}
.works,
.works__items {
  margin-left: -2px;
}
.section__line {
  height: 2px;
  width: 60%;
  margin: 0 auto 14px;
  background: linear-gradient(to right, #26a69a 0%, #5286a6 25%, #5286a6 50%, #8c5db4 75%, #ab48bc 100%);
}
.work {
  width: 20%;
  border-left: 2px solid transparent;
  border-bottom: 2px solid transparent;
  overflow: hidden;
}
.work__box-image {
  overflow: hidden;
  position: relative;
}
.work__box-image:after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: linear-gradient(to right, #26a69a 0%, #5286a6 25%, #5286a6 50%, #8c5db4 75%, #ab48bc 100%);
  opacity: 0.6;
  z-index: 1;
  transition: all 0.3s;
}
.work__item {
  display: block;
  font-size: 0;
}
.work__image {
  width: 100%;
  transition: all 0.7s;
}
.work:hover .work__box-image:after {
  opacity: 0;
}
.work:hover .work__button {
  bottom: 0;
}
.work__button {
  padding: 10px 5px;
  position: absolute;
  width: 100%;
  bottom: -82px;
  transition: all 0.5s;
  background: linear-gradient(to right, #26a69a 0%, #5286a6 25%, #5286a6 50%, #8c5db4 75%, #ab48bc 100%);
}
.work__button_text {
  text-transform: uppercase;
  font-size: 10px;
  text-align: center;
  color: #eee;
  font-weight: 600;
}
.work__line {
  border: 1px solid #eee;
  width: 80%;
  margin: 0 auto 10px auto;
}
.work__title {
  color: #eee;
  font-weight: 400;
}
.work__title_thumb {
  text-align: center;
}
.background__dark {
  background-color: #1a1a1a;
}
.__cov-progress {
  background-image: linear-gradient(to right, #26a69a 0%, #5286a6 25%, #5286a6 50%, #8c5db4 75%, #ab48bc 100%) !important;
}
.btn {
  background: linear-gradient(to right, #26a69a 0%, #5286a6 25%, #5286a6 50%, #8c5db4 75%, #ab48bc 100%);
  width: 80%;
  margin: 0 auto;
  padding: 8px 14px;
  text-align: center;
  font-weight: 500;
  color: #eee;
  font-size: 1rem;
  display: block;
  width: 100%;
  transition: all 0.5s;
}
.btn:hover {
  box-shadow: inset 0px 0px 43px 1px rgba(0,0,0,0.45);
  -webkit-transform: scale(0.96);
          transform: scale(0.96);
}
.btn__6 {
  width: 60%;
}
.btn__works {
  margin: 70px auto 70px;
}
@media screen and (max-width: 1200px) {
.work {
    width: 25%;
}
}
@media screen and (max-width: 992px) {
.work {
    width: 33.3%;
}
}
@media screen and (max-width: 768px) {
.work {
    width: 50%;
}
}
.section__title {
  text-align: left;
  margin-bottom: 28px;
  max-width: 500px;
}
.section__title .h3 {
  margin-bottom: 6px;
  color: #eee;
}
.section__title p {
  font-size: 12px;
  color: #9d9d9d;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.background {
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.header__slider .swiper-pagination-bullet {
  width: 46px;
  height: 22px;
  position: relative;
  background: transparent;
  opacity: 0.4;
}
.header__slider .swiper-pagination-bullet:after {
  content: '';
  background-color: #cfcbcb;
  top: 0;
  left: 0;
  width: 100%;
  height: 2px;
  position: absolute;
  margin: 10px 0;
}
.header__slider .swiper-pagination-bullet.swiper-pagination-bullet-active {
  opacity: 1;
}
.header__slider .swiper-pagination-bullet.swiper-pagination-bullet-active:after {
  background-color: #cfcbcb;
}
.header__slider .swiper-button-prev,
.header__slider .swiper-button-next {
  background-image: none;
  background-color: #333;
  width: 60px;
  height: 60px;
  border-radius: 50%;
  opacity: 0;
  transition: all 0.3s cubic-bezier(0.645, 0.045, 0.355, 1);
  -webkit-tap-highlight-color: transparent;
}
.header__slider .swiper-button-prev svg,
.header__slider .swiper-button-next svg {
  width: 20px;
  height: 20px;
  color: rgba(136,136,136,0.6);
  transition: color 0.3s;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-45%, -50%);
          transform: translate(-45%, -50%);
}
.header__slider .swiper-button-prev svg {
  -webkit-transform: translate(-55%, -50%);
          transform: translate(-55%, -50%);
}
.header__slider:hover .swiper-button-prev,
.header__slider:hover .swiper-button-next {
  opacity: 0.4;
}
.header__slider:hover .swiper-button-prev:hover,
.header__slider:hover .swiper-button-next:hover {
  opacity: 0.6;
  box-shadow: 0 1px 4px rgba(0,0,0,0.3), 0 0 40px rgba(0,0,0,0.1) inset;
  box-shadow: inset 0px 5px 10px 1px #000;
}
.header__slider:hover .swiper-button-prev:active,
.header__slider:hover .swiper-button-next:active {
  opacity: 0.4;
  box-shadow: none;
}
@media screen and (max-width: 580px) {
.header__slider .swiper-button-prev,
  .header__slider .swiper-button-next {
    background: none;
}
.header__slider .swiper-button-prev svg,
  .header__slider .swiper-button-next svg {
    color: #eee;
    width: 40px;
    height: 40px;
}
.header__slider:hover .swiper-button-prev:hover,
  .header__slider:hover .swiper-button-next:hover {
    box-shadow: none;
    opacity: 1;
}
}


